body{
    min-height: 100vh;
    background: black;
}

header{
    width: 100%;
    height: 10vh;
}

nav{
    height: 80%;
    width: 100%;
    display: flex;
    justify-content:right;
    align-items: center;
}

nav ul{
    display:flex;
    justify-content: center;
    align-items: center;
}

nav ul li{
    list-style: none;
    margin-left: 20px;
    margin-right: 20px;
    text-decoration: none;
    padding: 6px 15px;
    color: #fff;
    border-radius: 20px;
    font-size: 20px;
}

nav ul li:hover, nav ul li.active{
    background: #fff;
    color:black;
}

.navbar-container {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 10px;
    z-index: 9999;
    transition: background-color 0.3s ease;
}

.black-bg {
    background-color: #000000;
    color: #ffffff;
}
